import { PropertyOfflinePage } from '../pages/property-offline/property.offline';
import { OfflinePage } from '../pages/offline/offline';
import { EntitiesOfflineAllPage } from '../pages/entities-offline-all/entities.offline.all';

const __app_components__: any[] = [
  MyApp,
  TabsPage,
  LoginPage,
  MenuPage,
  HomePage,
  ProfilePage,
  StreetsPage,
  StreetsOfflinePage,
  StreetPropertiesOfflinePage,
  PropertiesOfflinePage,
  EntitiesOfflinePage,
  StreetOfflinePage,
  EntityOfflinePage,
  PropertyOfflinePage,
  NewStreetPage,
  PropertiesPage,
  NewPropertyPage,
  SecurityPage,
  SupportPage,
  StreetPage,
  StreetPhotoPage,
  PropertyPage,
  PropertyPhotoPage,
  EntityPage,
  EntitiesPage,
  NewEntityPage,
  EntityPhotoPage,
  SelectStreetPage,
  StreetPropertiesPage,
  OfflinePage,
  EntitiesOfflineAllPage
];
